body {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stopwatch__wrapper {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
  overflow: hidden;
}

.stopwatch__center {
  z-index: 2;
  background: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

.stopwatch__hand {
  z-index: 1;
  position: absolute;
  bottom: 50%;
}

.stopwatch__hand--seconds {
  transform-origin: bottom;
  background: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite seconds-move;
  left: calc(50% - .75vmin);
}

@keyframes seconds-move {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(1turn);
  }
}

.stopwatch__hand--minutes {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite minutes-move;
  left: calc(50% - 1.5vmin);
}

@keyframes minutes-move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up .stopwatch__hand--seconds {
  animation: 10s linear infinite seconds-move;
}

.stopwatch--speed-up .stopwatch__hand--minutes {
  animation: 600s steps(60, end) infinite minutes-move;
}
